.product__rating{
    display: flex;
}
.product{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: flex-end;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    margin: 10px;
    z-index: 1;
    padding: 20px;
    width: 100%;

}
.product >img{
     max-height:200px ;
     width: 100%;
     object-fit: contain;
     margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
}
.product__info{
    height: 100px;
    margin-bottom: 15px;
}
.product> button{
    background-color: #f0c14b;
    border:1px solid  ;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    color: #111
}
.product__rating > p {
    color: #ac841e;
    margin: 3px;
}