.header{
    background-color: rgb(12, 8, 27);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100  ;
}

.header__logo{
    width: 100px;
    object-fit: contain;
 margin-right: 20px;
 margin-top:18px ;
 margin-left:20px ;
    
       
}
.header__optionBasket{
    display: flex ;
    align-items: center  ;
}
.header__nav{
display: flex;
justify-content: space-evenly;
}
.header__option{
display: flex;
flex-direction: column;
margin-left: 10px;
margin-right: 10px;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header__link{
    color: white;
    text-decoration: none;
}

.header__searchIcon {
    padding: 5px;
    height: 22px;

    background-color:#cd9042
    
}
.header__search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}
.header__searchInput {
    height: 12px;
    padding: 10px;
    /* background-color: #cd9042; */
    border: none;
    width: 100%;
}
.header__optionBasket{
display: flex;
align-items: center;
color: white;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}